export var setConnection = [
  set(state`Connections.show`, false),
  set(state`Connections.oada_domain`, state`Connections.oada_domain_text`),
  //set(state`Connections.oada_fields_domain`, state`Connections.oada_fields_domain_text`), v2
  set(props`domain`, state`Connections.oada_domain`), // need to put domain in there for cerebralModule.resetCache
  cerebralModule.resetCache,
  init,
  set(props`domain`, state`Connections.oada_domain`)
];

export var cancelConnection = [
  set(state`Connections.show`, false),
  set(state`Connections.oada_domain`, state`Connections.oada_domain`),
  set(state`Connections.oada_fields_domain`,  state`Connections.oada_domain`),
];

export var showConnections = [
  set(state`Connections.show`, true)
];

function operationsCleanUp({state, props}){
  let operations =  state.get('operations.records');

  if(operations) {
    let operationsKeys = Object.keys(operations);
    let userOperations = state.get('User.operations');

    /* cleaning up operations from the previous connections */
    operationsKeys.forEach((operation) => {
        delete userOperations[operation];
    });

    state.set('User.operations', userOperations);
    state.set('operations.records', {});
    state.set('User.currentSelection.operation.id', "");
    state.set('User.currentSelection.operation.inputText', "");
    state.set('User.currentSelection.operation.placeHolder', "");
  }
}

function connectionCleanUp({state, props}){
  let connection_id = state.get('operations.connection_id');
  /* cleaning up connections from oada state */
  state.unset('oada.connections');
  /* cleaning up previous connection_id from oada state */
  state.unset('oada.' + connection_id)
}
